"use client"

import { useState, useEffect, useRef } from "react"
import { ArrowLeft, RotateCcw } from "lucide-react"
import * as THREE from "three"

interface NextNumberGameProps {
  onBack: () => void
}

interface Question {
  sequence: number[]
  options: number[]
  correct: number
}

const generateQuestions = (): Question[] => {
  const questions: Question[] = []

  const seq1 = [2, 4, 6, 8]
  questions.push({
    sequence: seq1,
    options: [9, 10, 11, 12],
    correct: 10,
  })

  const seq2 = [3, 6, 9, 12]
  questions.push({
    sequence: seq2,
    options: [14, 15, 16, 17],
    correct: 15,
  })

  const seq3 = [1, 1, 2, 3, 5]
  questions.push({
    sequence: seq3,
    options: [7, 8, 9, 10],
    correct: 8,
  })

  const seq4 = [1, 2, 4, 8]
  questions.push({
    sequence: seq4,
    options: [15, 16, 17, 18],
    correct: 16,
  })

  const seq5 = [1, 4, 9, 16]
  questions.push({
    sequence: seq5,
    options: [24, 25, 26, 27],
    correct: 25,
  })

  return questions
}

export default function NextNumberGame({ onBack }: NextNumberGameProps) {
  const [questions] = useState<Question[]>(generateQuestions())
  const [currentQuestion, setCurrentQuestion] = useState(0)
  const [score, setScore] = useState(0)
  const [answered, setAnswered] = useState(false)
  const [selectedAnswer, setSelectedAnswer] = useState<number | null>(null)
  const canvasRef = useRef<HTMLDivElement>(null)
  const mouseRef = useRef({ x: 0, y: 0 })

  useEffect(() => {
    if (!canvasRef.current) return

    const scene = new THREE.Scene()
    scene.fog = new THREE.Fog(0x0f172a, 100, 200)

    const camera = new THREE.PerspectiveCamera(
      75,
      canvasRef.current.clientWidth / canvasRef.current.clientHeight,
      0.1,
      1000,
    )
    const renderer = new THREE.WebGLRenderer({ antialias: true, alpha: true })

    renderer.setSize(canvasRef.current.clientWidth, canvasRef.current.clientHeight)
    renderer.setClearColor(0x0f172a, 0)
    renderer.outputColorSpace = THREE.SRGBColorSpace
    renderer.shadowMap.enabled = true
    renderer.shadowMap.type = THREE.PCFShadowMap
    canvasRef.current.appendChild(renderer.domElement)

    camera.position.z = 30

    const question = questions[currentQuestion]
    const spheres: THREE.Mesh[] = []

    question.sequence.forEach((num, idx) => {
      const group = new THREE.Group()

      const geometry = new THREE.SphereGeometry(1.2, 64, 64)
      const material = new THREE.MeshStandardMaterial({
        color: new THREE.Color().setHSL(idx / question.sequence.length, 0.8, 0.5),
        metalness: 0.3,
        roughness: 0.4,
        emissiveIntensity: 0.5,
      })
      material.emissive = new THREE.Color().setHSL(idx / question.sequence.length, 0.8, 0.3)

      const sphere = new THREE.Mesh(geometry, material)
      sphere.castShadow = true
      sphere.receiveShadow = true
      group.add(sphere)

      // Add glowing torus ring around sphere
      const torusGeom = new THREE.TorusGeometry(1.8, 0.15, 32, 100)
      const torusMat = new THREE.MeshBasicMaterial({
        color: new THREE.Color().setHSL(idx / question.sequence.length, 0.9, 0.6),
        transparent: true,
        opacity: 0.8,
      })
      const torus = new THREE.Mesh(torusGeom, torusMat)
      torus.userData.originalOpacity = 0.8
      group.add(torus)

      const angle = (idx / question.sequence.length) * Math.PI * 2
      group.position.x = Math.cos(angle) * 12
      group.position.y = Math.sin(angle) * 12
      group.userData = {
        targetX: group.position.x,
        targetY: group.position.y,
        number: num,
        index: idx,
        torusRing: torus,
      }

      scene.add(group)
      spheres.push(sphere)
    })

    // Enhanced lighting setup
    const light1 = new THREE.PointLight(0x00f3ff, 2, 150)
    light1.position.set(20, 20, 20)
    light1.castShadow = true
    scene.add(light1)

    const light2 = new THREE.PointLight(0xff006e, 2, 150)
    light2.position.set(-20, -20, 20)
    light2.castShadow = true
    scene.add(light2)

    const light3 = new THREE.PointLight(0x00ff88, 1.5, 100)
    light3.position.set(0, 25, 0)
    scene.add(light3)

    const ambientLight = new THREE.AmbientLight(0xffffff, 0.4)
    scene.add(ambientLight)

    // Mouse tracking for interactive effect
    const handleMouseMove = (e: MouseEvent) => {
      if (!canvasRef.current) return
      mouseRef.current.x = (e.clientX / canvasRef.current.clientWidth) * 2 - 1
      mouseRef.current.y = -(e.clientY / canvasRef.current.clientHeight) * 2 + 1
    }

    window.addEventListener("mousemove", handleMouseMove)

    const animate = () => {
      requestAnimationFrame(animate)

      scene.children.forEach((obj) => {
        if (obj instanceof THREE.Group && obj.userData.number !== undefined) {
          obj.rotation.x += 0.005
          obj.rotation.y += 0.005

          const time = Date.now() * 0.001
          obj.position.y = obj.userData.targetY + Math.sin(time + obj.userData.index) * 0.8
          obj.position.z = Math.sin(time * 0.5 + obj.userData.index) * 4

          // Add mouse influence
          obj.position.x += (mouseRef.current.x * 5 - obj.position.x * 0.02) * 0.01

          // Pulsing torus ring
          const pulsing = Math.sin(time * 2 + obj.userData.index) * 0.5 + 0.5
          obj.userData.torusRing.scale.z = 1 + pulsing * 0.3
          obj.userData.torusRing.material.opacity = pulsing * 0.8 + 0.2
        }
      })

      renderer.render(scene, camera)
    }

    animate()

    const handleResize = () => {
      if (canvasRef.current) {
        camera.aspect = canvasRef.current.clientWidth / canvasRef.current.clientHeight
        camera.updateProjectionMatrix()
        renderer.setSize(canvasRef.current.clientWidth, canvasRef.current.clientHeight)
      }
    }

    window.addEventListener("resize", handleResize)

    return () => {
      window.removeEventListener("resize", handleResize)
      window.removeEventListener("mousemove", handleMouseMove)
      renderer.dispose()
      canvasRef.current?.removeChild(renderer.domElement)
    }
  }, [currentQuestion, questions])

  const handleAnswer = (option: number) => {
    if (answered) return

    setSelectedAnswer(option)
    setAnswered(true)

    if (option === questions[currentQuestion].correct) {
      setScore(score + 1)
    }
  }

  const handleNext = () => {
    if (currentQuestion < questions.length - 1) {
      setCurrentQuestion(currentQuestion + 1)
      setAnswered(false)
      setSelectedAnswer(null)
    }
  }

  const handleRestart = () => {
    setCurrentQuestion(0)
    setScore(0)
    setAnswered(false)
    setSelectedAnswer(null)
  }

  const isGameComplete = currentQuestion === questions.length - 1 && answered

  return (
    <div className="relative w-full h-full flex flex-col">
      <div ref={canvasRef} className="absolute inset-0" />

      <div className="absolute top-6 left-6 flex gap-4 z-10">
        <button
          onClick={onBack}
          className="flex items-center gap-2 px-4 py-2 bg-blue-600 hover:bg-blue-700 text-white rounded-lg transition-all duration-300"
        >
          <ArrowLeft size={20} />
          Quay L·∫°i
        </button>
        <button
          onClick={handleRestart}
          className="flex items-center gap-2 px-4 py-2 bg-purple-600 hover:bg-purple-700 text-white rounded-lg transition-all duration-300"
        >
          <RotateCcw size={20} />
          L√†m L·∫°i
        </button>
      </div>

      <div className="absolute top-6 right-6 z-10">
        <div className="text-2xl font-bold text-yellow-300 glow">
          ‚≠ê ƒêi·ªÉm: {score}/{questions.length}
        </div>
      </div>

      <div className="absolute inset-0 flex flex-col items-center justify-center pointer-events-none">
        <div
          className="bg-slate-900/80 backdrop-blur-md p-8 rounded-2xl max-w-2xl pointer-events-auto border-2 border-blue-500/30"
          style={{
            boxShadow: "0 0 30px rgba(59, 130, 246, 0.3), inset 0 0 20px rgba(59, 130, 246, 0.1)",
          }}
        >
          <div className="mb-8">
            <p className="text-sm text-blue-300 mb-3">
              C√¢u {currentQuestion + 1}/{questions.length}
            </p>
            <p className="text-xl font-bold text-white mb-6">T√¨m s·ªë ti·∫øp theo trong d√£y:</p>

            <div className="flex gap-4 justify-center items-center mb-8 flex-wrap">
              {questions[currentQuestion].sequence.map((num, idx) => (
                <div key={idx} className="float-animation">
                  <div className="w-16 h-16 bg-gradient-to-br from-blue-500 to-blue-700 rounded-lg flex items-center justify-center text-2xl font-bold text-white glow">
                    {num}
                  </div>
                </div>
              ))}
              <div className="text-2xl text-gray-400 font-bold">?</div>
            </div>
          </div>

          {!answered ? (
            <div className="space-y-4">
              <p className="text-sm text-gray-300 mb-4">Ch·ªçn ƒë√°p √°n:</p>
              <div className="grid grid-cols-2 gap-4">
                {questions[currentQuestion].options.map((option) => (
                  <button
                    key={option}
                    onClick={() => handleAnswer(option)}
                    className="py-3 px-4 bg-gradient-to-r from-purple-600 to-pink-600 hover:from-purple-500 hover:to-pink-500 text-white font-bold rounded-lg transition-all duration-300 transform hover:scale-105 pulse-glow"
                  >
                    {option}
                  </button>
                ))}
              </div>
            </div>
          ) : (
            <div className="text-center">
              {selectedAnswer === questions[currentQuestion].correct ? (
                <div className="mb-6">
                  <div className="text-6xl mb-4">üéâ</div>
                  <p className="text-2xl font-bold text-green-400 mb-2">Ch√≠nh x√°c!</p>
                  <p className="text-gray-300">B·∫°n t√¨m ƒë√∫ng quy lu·∫≠t r·ªìi!</p>
                </div>
              ) : (
                <div className="mb-6">
                  <div className="text-6xl mb-4">üòä</div>
                  <p className="text-2xl font-bold text-orange-400 mb-2">Ch∆∞a ƒë√∫ng</p>
                  <p className="text-gray-300">
                    ƒê√°p √°n ƒë√∫ng l√†:{" "}
                    <span className="font-bold text-green-400">{questions[currentQuestion].correct}</span>
                  </p>
                </div>
              )}

              {!isGameComplete && (
                <button
                  onClick={handleNext}
                  className="mt-6 px-6 py-2 bg-green-600 hover:bg-green-700 text-white font-bold rounded-lg transition-all duration-300"
                >
                  C√¢u Ti·∫øp Theo ‚Üí
                </button>
              )}
            </div>
          )}

          {isGameComplete && (
            <div className="text-center mt-6">
              <p className="text-xl text-gray-300 mb-4">B·∫°n ho√†n th√†nh tr√≤ ch∆°i v·ªõi {score} ƒëi·ªÉm!</p>
              <button
                onClick={handleRestart}
                className="px-6 py-2 bg-blue-600 hover:bg-blue-700 text-white font-bold rounded-lg transition-all duration-300"
              >
                Ch∆°i L·∫°i
              </button>
            </div>
          )}
        </div>
      </div>
    </div>
  )
}
