"use client"

import { useState, useEffect, useRef } from "react"
import { ArrowLeft, RotateCcw } from "lucide-react"
import * as THREE from "three"

interface MissingNumberGameProps {
  onBack: () => void
}

interface Question {
  sequence: (number | null)[]
  missingIndex: number
  correct: number
  options: number[]
}

const generateQuestions = (): Question[] => {
  const questions: Question[] = []

  questions.push({
    sequence: [2, 4, null, 8],
    missingIndex: 2,
    correct: 6,
    options: [5, 6, 7, 8],
  })

  questions.push({
    sequence: [null, 6, 9, 12],
    missingIndex: 0,
    correct: 3,
    options: [1, 2, 3, 4],
  })

  questions.push({
    sequence: [1, null, 2, 3, 5],
    missingIndex: 1,
    correct: 1,
    options: [0, 1, 2, 3],
  })

  questions.push({
    sequence: [5, 10, null, 20],
    missingIndex: 2,
    correct: 15,
    options: [12, 13, 14, 15],
  })

  questions.push({
    sequence: [1, null, 4, 8],
    missingIndex: 1,
    correct: 2,
    options: [2, 3, 4, 5],
  })

  return questions
}

export default function MissingNumberGame({ onBack }: MissingNumberGameProps) {
  const [questions] = useState<Question[]>(generateQuestions())
  const [currentQuestion, setCurrentQuestion] = useState(0)
  const [score, setScore] = useState(0)
  const [answered, setAnswered] = useState(false)
  const [selectedAnswer, setSelectedAnswer] = useState<number | null>(null)
  const canvasRef = useRef<HTMLDivElement>(null)
  const mouseRef = useRef({ x: 0, y: 0 })

  useEffect(() => {
    if (!canvasRef.current) return

    const scene = new THREE.Scene()
    scene.fog = new THREE.Fog(0x0f172a, 80, 200)

    const camera = new THREE.PerspectiveCamera(
      75,
      canvasRef.current.clientWidth / canvasRef.current.clientHeight,
      0.1,
      1000,
    )
    const renderer = new THREE.WebGLRenderer({ antialias: true, alpha: true })

    renderer.setSize(canvasRef.current.clientWidth, canvasRef.current.clientHeight)
    renderer.setClearColor(0x0f172a, 0)
    renderer.outputColorSpace = THREE.SRGBColorSpace
    renderer.shadowMap.enabled = true
    canvasRef.current.appendChild(renderer.domElement)

    camera.position.z = 30

    const question = questions[currentQuestion]
    const cubes: THREE.Mesh[] = []

    question.sequence.forEach((num, idx) => {
      const group = new THREE.Group()

      const geometry = new THREE.BoxGeometry(1.5, 1.5, 1.5)
      const isNull = num === null

      const material = new THREE.MeshStandardMaterial({
        color: isNull ? 0xff006e : new THREE.Color().setHSL(Math.random(), 0.7, 0.5),
        metalness: isNull ? 0.6 : 0.4,
        roughness: isNull ? 0.2 : 0.5,
        emissiveIntensity: 0.3,
      })
      material.emissive = isNull ? new THREE.Color(0xff006e).multiplyScalar(0.3) : new THREE.Color(0x000000)

      const cube = new THREE.Mesh(geometry, material)
      cube.castShadow = true
      cube.receiveShadow = true
      group.add(cube)

      // Add edge glow for missing numbers
      if (isNull) {
        const edgeGeom = new THREE.EdgesGeometry(geometry)
        const lineMat = new THREE.LineBasicMaterial({ color: 0xff006e, linewidth: 2 })
        const wireframe = new THREE.LineSegments(edgeGeom, lineMat)
        group.add(wireframe)
      }

      const startX = -10 + idx * 5
      group.position.x = startX
      group.position.y = 0
      group.userData = {
        targetX: startX,
        number: num,
        index: idx,
        isNull,
      }

      scene.add(group)
      cubes.push(cube)
    })

    // Enhanced lighting
    const light1 = new THREE.PointLight(0x00f3ff, 2.5, 150)
    light1.position.set(15, 15, 15)
    light1.castShadow = true
    scene.add(light1)

    const light2 = new THREE.PointLight(0xff006e, 2.5, 150)
    light2.position.set(-15, -15, 15)
    light2.castShadow = true
    scene.add(light2)

    const light3 = new THREE.DirectionalLight(0xffffff, 0.5)
    light3.position.set(0, 30, 0)
    light3.castShadow = true
    scene.add(light3)

    const ambientLight = new THREE.AmbientLight(0xffffff, 0.5)
    scene.add(ambientLight)

    const handleMouseMove = (e: MouseEvent) => {
      if (!canvasRef.current) return
      mouseRef.current.x = (e.clientX / canvasRef.current.clientWidth) * 2 - 1
      mouseRef.current.y = -(e.clientY / canvasRef.current.clientHeight) * 2 + 1
    }

    window.addEventListener("mousemove", handleMouseMove)

    const animate = () => {
      requestAnimationFrame(animate)

      scene.children.forEach((obj) => {
        if (obj instanceof THREE.Group && obj.userData.isNull !== undefined) {
          obj.rotation.x += 0.003
          obj.rotation.y += 0.003

          const time = Date.now() * 0.001
          obj.position.y = Math.sin(time + obj.userData.index * 0.5) * 2.5
          obj.position.z = Math.cos(time * 0.7 + obj.userData.index) * 2.5

          // Mouse interaction
          if (obj.userData.isNull) {
            obj.position.x += (mouseRef.current.x * 3 - obj.position.x * 0.01) * 0.02
          }

          // Pulsing for missing cube
          if (obj.userData.isNull) {
            const pulse = Math.sin(time * 3) * 0.3 + 1
            obj.scale.copy(new THREE.Vector3(pulse, pulse, pulse))
          }
        }
      })

      renderer.render(scene, camera)
    }

    animate()

    const handleResize = () => {
      if (canvasRef.current) {
        camera.aspect = canvasRef.current.clientWidth / canvasRef.current.clientHeight
        camera.updateProjectionMatrix()
        renderer.setSize(canvasRef.current.clientWidth, canvasRef.current.clientHeight)
      }
    }

    window.addEventListener("resize", handleResize)

    return () => {
      window.removeEventListener("resize", handleResize)
      window.removeEventListener("mousemove", handleMouseMove)
      renderer.dispose()
      canvasRef.current?.removeChild(renderer.domElement)
    }
  }, [currentQuestion, questions])

  const handleAnswer = (option: number) => {
    if (answered) return

    setSelectedAnswer(option)
    setAnswered(true)

    if (option === questions[currentQuestion].correct) {
      setScore(score + 1)
    }
  }

  const handleNext = () => {
    if (currentQuestion < questions.length - 1) {
      setCurrentQuestion(currentQuestion + 1)
      setAnswered(false)
      setSelectedAnswer(null)
    }
  }

  const handleRestart = () => {
    setCurrentQuestion(0)
    setScore(0)
    setAnswered(false)
    setSelectedAnswer(null)
  }

  const isGameComplete = currentQuestion === questions.length - 1 && answered

  return (
    <div className="relative w-full h-full flex flex-col">
      <div ref={canvasRef} className="absolute inset-0" />

      <div className="absolute top-6 left-6 flex gap-4 z-10">
        <button
          onClick={onBack}
          className="flex items-center gap-2 px-4 py-2 bg-pink-600 hover:bg-pink-700 text-white rounded-lg transition-all duration-300"
        >
          <ArrowLeft size={20} />
          Quay L·∫°i
        </button>
        <button
          onClick={handleRestart}
          className="flex items-center gap-2 px-4 py-2 bg-purple-600 hover:bg-purple-700 text-white rounded-lg transition-all duration-300"
        >
          <RotateCcw size={20} />
          L√†m L·∫°i
        </button>
      </div>

      <div className="absolute top-6 right-6 z-10">
        <div className="text-2xl font-bold text-yellow-300 glow">
          ‚≠ê ƒêi·ªÉm: {score}/{questions.length}
        </div>
      </div>

      <div className="absolute inset-0 flex flex-col items-center justify-center pointer-events-none">
        <div
          className="bg-slate-900/80 backdrop-blur-md p-8 rounded-2xl max-w-2xl pointer-events-auto border-2 border-pink-500/30"
          style={{
            boxShadow: "0 0 30px rgba(236, 72, 153, 0.3), inset 0 0 20px rgba(236, 72, 153, 0.1)",
          }}
        >
          <div className="mb-8">
            <p className="text-sm text-pink-300 mb-3">
              C√¢u {currentQuestion + 1}/{questions.length}
            </p>
            <p className="text-xl font-bold text-white mb-6">T√¨m s·ªë c√≤n thi·∫øu trong d√£y:</p>

            <div className="flex gap-4 justify-center items-center mb-8 flex-wrap">
              {questions[currentQuestion].sequence.map((num, idx) => (
                <div key={idx} className="float-animation">
                  {num !== null ? (
                    <div className="w-16 h-16 bg-gradient-to-br from-pink-500 to-pink-700 rounded-lg flex items-center justify-center text-2xl font-bold text-white glow">
                      {num}
                    </div>
                  ) : (
                    <div className="w-16 h-16 bg-gradient-to-br from-gray-700 to-gray-900 rounded-lg flex items-center justify-center text-2xl font-bold text-pink-400 border-2 border-pink-400 border-dashed glow">
                      ?
                    </div>
                  )}
                </div>
              ))}
            </div>
          </div>

          {!answered ? (
            <div className="space-y-4">
              <p className="text-sm text-gray-300 mb-4">Ch·ªçn ƒë√°p √°n:</p>
              <div className="grid grid-cols-2 gap-4">
                {questions[currentQuestion].options.map((option) => (
                  <button
                    key={option}
                    onClick={() => handleAnswer(option)}
                    className="py-3 px-4 bg-gradient-to-r from-pink-600 to-rose-600 hover:from-pink-500 hover:to-rose-500 text-white font-bold rounded-lg transition-all duration-300 transform hover:scale-105 pulse-glow"
                  >
                    {option}
                  </button>
                ))}
              </div>
            </div>
          ) : (
            <div className="text-center">
              {selectedAnswer === questions[currentQuestion].correct ? (
                <div className="mb-6">
                  <div className="text-6xl mb-4">üåü</div>
                  <p className="text-2xl font-bold text-green-400 mb-2">Si√™u ƒê√∫ng!</p>
                  <p className="text-gray-300">B·∫°n t√¨m th·∫•y s·ªë c√≤n thi·∫øu r·ªìi!</p>
                </div>
              ) : (
                <div className="mb-6">
                  <div className="text-6xl mb-4">ü§î</div>
                  <p className="text-2xl font-bold text-orange-400 mb-2">Ch∆∞a ch√≠nh x√°c</p>
                  <p className="text-gray-300">
                    S·ªë ƒë√∫ng l√†: <span className="font-bold text-green-400">{questions[currentQuestion].correct}</span>
                  </p>
                </div>
              )}

              {!isGameComplete && (
                <button
                  onClick={handleNext}
                  className="mt-6 px-6 py-2 bg-green-600 hover:bg-green-700 text-white font-bold rounded-lg transition-all duration-300"
                >
                  C√¢u Ti·∫øp Theo ‚Üí
                </button>
              )}
            </div>
          )}

          {isGameComplete && (
            <div className="text-center mt-6">
              <p className="text-xl text-gray-300 mb-4">B·∫°n ho√†n th√†nh tr√≤ ch∆°i v·ªõi {score} ƒëi·ªÉm!</p>
              <button
                onClick={handleRestart}
                className="px-6 py-2 bg-pink-600 hover:bg-pink-700 text-white font-bold rounded-lg transition-all duration-300"
              >
                Ch∆°i L·∫°i
              </button>
            </div>
          )}
        </div>
      </div>
    </div>
  )
}
